import { API_CONFIG } from 'config/core/api.config';
import { HttpClient } from "@angular/common/http";
import { Observable } from "rxjs/Rx";
import { Injectable } from '@angular/core';
import { UnidadeMedidaDTO } from './../../models/unidade-medida.dto';


@Injectable()
export class UnidadeMedidaService {

    constructor(public http: HttpClient) {
    }

    findAll() : Observable<UnidadeMedidaDTO[]>  {
        return this.http.get<UnidadeMedidaDTO[]>(`${API_CONFIG.baseUrl}/unidades/all`);
    }
    findAllAtivo() : Observable<UnidadeMedidaDTO[]>  {
        return this.http.get<UnidadeMedidaDTO[]>(`${API_CONFIG.baseUrl}/unidades/`);
    }
}



