
import { ComboDTO } from 'models/combo.dto';
import { FormGroup, FormBuilder, Validators } from '@angular/forms';
import { Component, OnInit, ViewChild } from '@angular/core';
import { Router } from '@angular/router';
import { Location } from '@angular/common';
import { EventEmitterService } from 'app/shared/event/event-emitter.services';
import { SelectComponent, SelectItem } from 'ng2-select';


import { AbstractBaseComponent } from '../../../../config/core/abstract-base.component';
import { CategoriaDTO } from 'models/categoria.dto';
import { UnidadeMedidaDTO } from '../../../../models/unidade-medida.dto';
import { ProdutoComposicaoDTO } from '../../../../models/produto-composicao.dto';
import { ProdutoDTO } from 'models/produto.dto';

import { CategoriasService } from './../../categorias/categoria.service';
import { UnidadeMedidaService } from '../../../../services/domain/unidade.service';
import { ProdutosService } from './../produtos.service';



@Component({
  selector: 'app-create-catetorias',
  templateUrl: './create-produtos.component.html',
  styleUrls: ['./create-produtos.component.scss']
})
export class CreateProdutosComponent extends AbstractBaseComponent implements OnInit {



  @ViewChild('ModalCompProd') modalCompProd;

  public categorias: CategoriaDTO[];
  public produtosComposicoes: ProdutoComposicaoDTO[] = [];
  public formProdComp: FormGroup;

  public prodComp: ComboDTO;
  public comboCategorias: ComboDTO[] = []
  public comboCompUnidades: ComboDTO[] = []
  public comboCompProdutos: ComboDTO[] = []

  public checkUsoInterno: boolean = false;
  public checkProdutoComposto: boolean = false;
  public checkMostrar: boolean = true;
  public alterarComposicao: boolean = false;


  private value: any = ['Athens'];
  constructor(
    public fb: FormBuilder,
    public location: Location,
    public categoriasService: CategoriasService,
    public unidadeService: UnidadeMedidaService,
    public produtosService: ProdutosService,
    public router: Router) {
    super();
  }

  ngOnInit() {
    this.criarFormulario();
    this.criarFormProdComp();

    this.categoriasService.findAll()
      .subscribe(
        response => { this.comboCategorias = this.preencharCombo(response, 'id', 'nome'); }
        , error => console.log(error));


    this.produtosService.findAllProdutoInterno()
      .subscribe(
        response => { this.comboCompProdutos = this.preencharCombo(response, 'id', 'nmProduto'); }
        , error => console.log(error));

    this.unidadeService.findAllAtivo()
      .subscribe(
        response => { this.comboCompUnidades = this.preencharCombo(response, 'id', 'sigla'); }
        , error => console.log(error))
  }

  confirmar(value: any) {
    //let categoria: CategoriaDTO = {id:'1', nome:'2das'};
    
    let prod = {} as ProdutoDTO;
    prod.id = value.idProduto;
    prod.nmProduto = value.nmProduto;
    prod.preco = value.preco;
    prod.produtoComposto = value.produtoComposto;
    prod.status = 'A';
    prod.usoInterno = value.usoInterno;
    prod.categoria = { id: value.categoria[0].id, nome: value.categoria[0].text }
    prod.urlImagem = this.urlFoto;
    //composicoes: ProdutoComposicaoDTO[],

    this.produtosService.salvar(prod)
      .subscribe(response => {
        EventEmitterService.emitirMensagemToasterSucesso('Categorias', 'Categoria incluída com sucesso!!!');
        this.router.navigate(['/produtos/listar']);
      },
        error => {
          console.log('error de validação:', error.error);
        });

  }

  public voltar() {
    this.location.back();
  }
  mudarImage(evento) {
    this.atualizarFoto(evento.foto);
  }

  private atualizarFoto(foto: any) {
    this.urlFoto = foto;
  }


  private criarFormulario() {
    this.formulario = this.fb.group({
      idProduto: [''],
      nmProduto: ['', [Validators.required, Validators.minLength(5), Validators.maxLength(25)]],
      categoria: ['', Validators.required],
      preco: ['', Validators.required],
      produtoComposto: ['N'],
      status: ['A'],
      usoInterno: ['N'],
      urlImagem: ['']
    })
  }


  public criarFormProdComp() {
    this.formProdComp = this.fb.group({
      idProdutoPai: [],
      produtoComp: ['', Validators.required],
      unidade: ['', Validators.required],
      qtdUtilizada: ['', Validators.required],
      mostrarUnidade: ['S']
    })
  }

  public alterarCheckUsoInterno(valor: boolean) {
    this.formulario.patchValue({
      usoInterno: valor ? 'S' : 'N'
    });
  }
  public alterarCheckProdutoComposto(valor: boolean) {
    this.checkProdutoComposto = valor;
    this.formulario.patchValue({
      produtoComposto: valor ? 'S' : 'N'
    });
  }

  public compProdCheckMostrar(valor: boolean) {
    this.formProdComp.patchValue({
      mostrarUnidade: valor ? 'S' : 'N'
    });
  }

  public mostrarPopupCompProd() {
    this.criarFormProdComp();
    this.modalCompProd.show()
  }



  confirmarCompProd(value: any) {
    let prod = {} as ProdutoComposicaoDTO;
    prod.idProduto = value.idProdutoPai;
    prod.idProdutoComposicao = value.produtoComp[0].id;
    prod.nmProdutoComposicao = value.produtoComp[0].text;
    prod.idUnidadeMedida = value.unidade[0].id;
    prod.nmUnidadeMedida = value.unidade[0].text;
    prod.qtdUtilizada = Number(value.qtdUtilizada)
    prod.mostrarUnidade = value.mostrarUnidade

    let position = this.produtosComposicoes.findIndex(x => x.idProdutoComposicao == prod.idProdutoComposicao);
    if (position == -1) {
      this.produtosComposicoes.push(prod);
    } else {
      if (this.alterarComposicao) {
        this.produtosComposicoes[position] = prod;
        this.alterarComposicao = false;
      } else {
        this.produtosComposicoes[position].qtdUtilizada += prod.qtdUtilizada
      }
    }
    this.modalCompProd.hide();
  }


  excluirItem(item: ProdutoComposicaoDTO) {
    this.produtosComposicoes =
      this.produtosComposicoes
        .filter((prod: ProdutoComposicaoDTO) => prod.idProdutoComposicao != item.idProdutoComposicao)
  }

  alterarProdutoComp(item: ProdutoComposicaoDTO) {
    this.alterarComposicao = true;
    this.formProdComp.patchValue({
      produtoComp: [{ id: item.idProdutoComposicao, text: item.nmProdutoComposicao, disabled: true }],
      unidade: [{ id: item.idUnidadeMedida, text: item.nmUnidadeMedida }],
      qtdUtilizada: item.qtdUtilizada,
      mostrarUnidade: item.mostrarUnidade
    });
    this.modalCompProd.show();
  }
}



